import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { HomePage } from '../../pages/home/home'
import { NativeStorage } from '@ionic-native/native-storage';
import { empty } from 'rxjs/Observer';
/*
  Generated class for the RestProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class RestProvider {
  data_prov: any;
  item: any;
  apiUrl = 'http://services-spa.azurewebsites.net/api/values';
  UrlProvince = 'http://services-spa.azurewebsites.net/api/province';
  constructor(public http: HttpClient,
              public nativeStorage: NativeStorage,
              
              ) {
    
  }
  getData() {
    return new Promise(resolve => {
      this.http.get(this.apiUrl).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }
  getProvince(){
    return new Promise(resolve => {
      this.http.get(this.UrlProvince).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  getAmphur(){
    /*this.nativeStorage.getItem('data_prov').then((data_prov) =>{
      this.data_prov={
        prov_id : data_prov.prov_id
      } 
    })
    console.log(this.data_prov.prov_id);*/
   
    var x = 1;
    return new Promise(resolve => {
      this.http.get(this.UrlProvince+'/'+x).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
    
  }
}



